@use "./footer.scss";
html,
body,
div,
p,
img,
h1,
h2,
h3,
h4,
h5 {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: sans-serif;
}

h1 {
  font-size: 2em;
}
.container {
  width: 980px;
  max-width: 100%;
  margin: 0 auto;
  padding: 0.5em;
}
.main-image {
  background-image: url("../images/thumbnail.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center ;
  position: relative;
  width: 100%;
  height: 500px;
  border-radius: 2em;
  margin: 1em 0;
}

.icon {
  background-color: #fff;
  width: 64px;
  height: 64px;
  border-radius: 32px;
  padding: 4px;
  position: absolute;
  top: 1em;
  left: 1em;
}
